@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html,
body {
    font-size: 16px;
    font-family: Montserrat, sans-serif;
    color: #f2f2f2;
    background: rgb(48,165,238);
    background: linear-gradient(180deg, rgba(48,165,238,1) 0%, rgba(226,167,196,1) 100%, rgba(244,150,46,1) 100%);  
}

.Search {
margin-top: 2rem;
  max-width: 500px;
  display: flex;
  justify-content: center;
}

.Search__button,
.Search__input {
  border: none;
  outline: none;
  font-size:  0.9rem;
  background-color: transparent;
  color: #fff;
  font-weight: 500;
}

.Search__input {
  width: 100%;
  padding: 0.75rem 0rem 0.75rem 1.75rem;
  font-weight: 600;
  border: 1px solid #fff;
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
}

.Search__input::-webkit-input-placeholder {
  color: #f2f2f2;
  opacity: 0.85;
}

.Search__input:-ms-input-placeholder {
  color: #f2f2f2;
  opacity: 0.85;
}

.Search__input::placeholder {
  color: #f2f2f2;
  opacity: 0.85;
}

.Search__button {
  padding: 0.75rem;
  font-weight: 500;
  background-color: #ECA39D;
  border: 1px solid #e9b4af;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
  width: 25%;
  cursor: pointer;
}

.Search__button:disabled {
  background-color: darkgrey;
  cursor: not-allowed;
}
.Current {
  display: flex;
  justify-content: space-between;
  padding: 5rem 0 2rem;
}

.Current__info {
  display: flex;
  width: 100%;
  justify-content: flex-start;
}

.Current__weather-icon {
  width: 80px;
  height: 80px;

  
}

.other-info__city {
  font-size: 1.15rem;
  margin-bottom: 1rem;
  text-align: right;
}


.RequestsHistory {
	display: block;
}

.RequestsHistory__title {
	margin-bottom: 2rem;
	font-size: 1.2rem;
}

.RequestsHistory__queries {
  display: flex;
  flex-wrap: wrap;
}

.RequestsHistory__queries.item {
	border: 1px solid white;
	border-radius: 13px;
	padding: 6px 12px;
	background: #eca39d;
	cursor: pointer;
  margin: 0 0.5rem 1rem;
}

.RequestsHistory__queries.item:hover {
	text-decoration: underline;
}

.ForecastDay {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	color: #f2f2f2;
  margin: 1rem 0;
}

.ForecastDay__title {
	width: 100%;
	font-size: 1rem;
	text-align: center;
  font-weight: 600;
}

.ForecastDay__header {
  display: flex;
  justify-content: center;
  align-items: center;
}

.ForecastDay__weather-icon {
	width: 80px;
	height: 80px;
}

.ForecastDay__weather-icon:hover {
	animation-name: icons;
	animation-duration: 1s;
	animation-iteration-count: infinite;
}

@keyframes icons {
	0% {
		position: relative;
		top: 0;
	}
	50% {
		position: relative;
		top: 0.25rem;
	}
	100% {
		position: relative;
		top: 0;
	}
}

.Forecast{
  display: flex;
  justify-content:center;
  flex-wrap: wrap;
}

.App {
  min-height: 100vh;

}

.App__container {
  padding: 30px;
  max-width: 1440px;
  min-height: 100vh;
  margin: 0 auto;
}


